  display: block;
  top: 0;
  left: 0;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  overflow: hidden;
}
.envelope .paper.back {
  top: 0;
}
.envelope .paper.back:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  margin-bottom: -1px;
  border-style: solid;
  border-width: 0 15em 10em 15em;
  border-color: transparent transparent #d3d3d3 transparent;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  z-index: 0;
}
.envelope .paper.back:after {
  content: "";
  display: block;
  background-color: #d3d3d3;
  width: 30em;
  height: 18em;
}
.envelope .paper.front {
  top: 10em;
  box-shadow: 0.1em 0.5em 0.5em rgba(0, 0, 0, 0.25);
  z-index: 0;
}
.envelope .paper.front:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10em 15em 0 15em;
  border-color: transparent #fff;
}
.envelope .paper.front:after {
  content: "";
  display: block;
  width: 30em;
  height: 8em;
  background: #fff;
  margin-top: -1px;
}
@-webkit-keyframes envelope-front {
  0 {
    top: 5em;
    z-index: 0;
  }
  50% {
    top: 15em;
    z-index: 9;
  }
  100% {
    top: 5em;
    z-index: 9;
  }
}
@keyframes envelope-front {
  0 {
